import './netWorkHint.less'
import hint from '../../assets/img/hint.svg'
import { DeriEnv } from '../../lib/web3js'
import useConfig from '../../hooks/useConfig'
const Env = DeriEnv.get()
export default function NetWorkHint({ wallet, isFutrue }) {
  const config = useConfig();
  const changeNetWork = (value) => {
    let id = Env === 'prod' ? value === 'bsc' ? 56 : 42161 : value === 'bsc' ? 97 : 421611
    wallet.switchNetwork(config[id])
  }
  return (
    <div className='network-hint-box'>
      <div className='hint-img'>
        <img src={hint} />
        <div className='hint-text'>ErrorWrong network. Please switch to <span className='network-name' onClick={() => { changeNetWork('arb') }}>Arbitrum</span> or <span className='network-name'  onClick={() => { changeNetWork('bsc') }}> BNB Chain</span></div>
      </div>
    </div>
  )
}